form {
  .formActions {
    grid-column: span 4 / span 4;
  }
  .formActions {
    display: flex;
  }
  .formActions {
    align-items: center;
  }
  .formActions {
    justify-content: space-around;
  }
  .formActions {

    label {
      .expressText {
        transition: all 0.3s;
      }

      .expressTextOn {
        text-shadow: 0 0 0.5rem #fff;
      }

      .expressTextOff {
        --tw-text-opacity: 1;
        color: var(--fallback-bc,oklch(var(--bc)/var(--tw-text-opacity)));
      }

      .expressTextOff {
        --tw-text-opacity: 0.5;
      }
    }
  }
}

